// import React from 'react';
import { Gallery, Item } from 'react-photoswipe-gallery';
import 'photoswipe-dynamic-caption-plugin/photoswipe-dynamic-caption-plugin.css'
// @ts-expect-error
import PhotoSwipeDynamicCaption from 'photoswipe-dynamic-caption-plugin'


// TODO: reflexでインポートすると_が何故か使えない
export interface CounterProps {
    originala: string;
    thumbnaila: string;
    originalb: string;
    thumbnailb: string;
    widtha: string;
    heighta: string;
    widthb: string;
    heightb: string;
}

// const smallItemStyles: React.CSSProperties = {
//     // cursor: 'pointer',
//     // objectFit: 'cover',
//     // width: '100%',
//     // height: 'auto',
//     cursor: 'pointer',
//     objectFit: 'cover',
//     width: '150px',
//     height: '150px',
// };

const smallItemStyles: React.CSSProperties = {
    cursor: 'pointer',
    objectFit: 'cover',
    width: '150px',
    height: '150px',
}

export const Counter = (props: CounterProps) => (
    <Gallery>
        <div
            style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(3, 0fr)',
                gridGap: 10,
            }}
        >
            <Item<HTMLImageElement>
                cropped
                original={props.originala}
                thumbnail={props.thumbnaila}
                width={props.widtha}
                height={props.heighta}
            >
                {({ ref, open }) => (
                    <img
                        style={smallItemStyles}
                        src={props.thumbnaila}
                        ref={ref}
                        onClick={open}
                    />
                )}
            </Item>
            <Item<HTMLImageElement>
                cropped
                original={props.originalb}
                thumbnail={props.thumbnailb}
                width={props.widthb}
                height={props.heightb}
            >
                {({ ref, open }) => (
                    <img
                        style={smallItemStyles}
                        src={props.thumbnailb}
                        ref={ref}
                        onClick={open}
                    />
                )}
            </Item>
        </div>
    </Gallery >
)

